.services-contact {
  margin-top: 50px;
  margin-bottom: 50px;
}

.services-txt {
  font-size: 20px;
  text-align: justify;
}

// If screen is 900px or less, make font size 16px
@media (max-width: 900px) {
  .services-txt {
    font-size: 16px;
  }
}

.scroller {
  height: 600px;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
}

.scroller section {
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 15%;
  text-align: center;
  scroll-snap-align: center;
}

// If screen size is 900 px or less, make scroll-snap-type start
@media (max-width: 900px) {
  .scroller {
    scroll-snap-align: start;
  }

  // Make all buttons that are deeply nested children of .services-txt 12px
  .scroller .btn-lg {
    font-size: 14px !important;
  }
}

section {
  // Make height 100% of viewport height minus 254 pixels
  min-height: 600px;
  justify-content: center;
  align-items: center;
}

.scroll-caret:hover {
  filter: opacity(0.3);
  cursor: pointer;
}